n            class=\"message is-danger\">\n            <div class=\"message-body\">\n              {{ error }}\n            </div>\n          </div>\n\n          <b-field>\n            <b-input\n              id=\"first_name\"\n              v-model=\"form.first_name\"\n              ttpe=\"text\"\n              required\n              autofocus\n              size=\"is-medium\"\n              placeholder=\"First name\"\n            />\n          </b-field>\n\n          <b-field>\n            <b-input\n              id=\"email\"\n              v-model=\"form.email\"\n              ttpe=\"text\"\n              required\n              autofocus\n              size=\"is-medium\"\n              placeholder=\"Email\"\n            />\n          </b-field>\n\n          <b-field>\n            <b-input\n              id=\"password\"\n              v-model=\"form.password\"\n              type=\"password\"\n              required\n              password-reveal\n              size=\"is-medium\"\n              placeholder=\"Password\"\n            />\n          </b-field>\n\n          <b-field>\n            <b-input\n              id=\"confirm_password\"\n              v-model=\"form.confirm_password\"\n              type=\"password\"\n              password-reveal\n              required\n              size=\"is-medium\"\n              placeholder=\"Confirm password\"\n            />\n          </b-field>\n\n          <b-field class=\"has-text-left\">\n            <b-checkbox v-model=\"form.checked\">\n              Remember me\n            </b-checkbox>\n            <!--<a style=\"float: right\" href=\"/\">login?</a>-->\n          </b-field>\n\n\n          <button\n            id=\"btn_sign_up\"\n            :class=\"{'is-loading': isLoading, 'is-disabled': isLoading}\"\n            class=\"button is-block is-primary is-outlined is-medium is-fullwidth\"\n            @click=\"onSubmit($event)\">\n            Sign up\n          </button>\n\n        </form>\n      </div>\n    </div>\n  </div>\n</template>\n\n\n<script>\n  import gdprConsent from \"@/components/GdprConsent\";\n  import GoogleSignInButton from 'vue-google-signin-button-directive'\n\n  export default {\n    name: 'SignUp',\n    components: {\n      gdprConsent\n    },\n    directives: {\n      GoogleSignInButton\n    },\n    data() {\n      return {\n        gdprConsented: Vue.cookie.get('gdprConsented'),\n        error: null,\n        form: {\n          first_name: '',\n          email: '',\n          username: '',\n          password: '',\n          confirm_password: ''\n        },\n        redirect: null,\n        isLoading: false,\n        googleIsLoading: false,\n        googleSignInParams: {\n          client_id: '476667578043-0tm7eoufgmi6m961qb2m77hhelj5ksl5.apps.googleusercontent.com',\n          response_type: 'permission'\n        }\n      }\n    },\n    created() {\n      // this.$store.dispatch('setLayout', 'front-layout')\n    },\n\n    methods: {\n      onSubmit(evt) {\n        this.isLoading = true\n        evt.preventDefault()\n        this.$auth.register({\n          body: this.form,\n          autoLogin: true,\n          redirect: this.redirect || {name: 'FrontPage'},\n          rememberMe: this.form.checked\n        }).then((res) => {\n          this.$mixpanel.alias(res.body.data.id);\n          document.querySelector('button.modal-close').click();\n          this.$store.dispatch(\"setUser\", res.body.data);\n        }, (res) => {\n          console.log('error ' + this.context);\n          this.error = res.data.message;\n          this.isLoading = false\n        });\n      },\n      onClick(e) {\n        e.preventDefault();\n        this.$router.push('/')\n      },\n      OnGoogleAuthSuccess(access_token) {\n        this.$store.dispatch(\"setUserFromGoogle\", {\n          access_token,\n          pid: this.form.pid,\n        }).then((res) => {\n          if (!res.userExisted) {\n            this.$mixpanel.alias(res.user.id+'');\n          }\n          this.$store.dispatch(\"setUser\", res.user);\n          this.$auth.user(res.user)\n          this.$auth.token(null, res.token)\n          this.$auth.watch.authenticated = true\n          this.$auth.watch.loaded = true\n          this.googleIsLoading = false;\n          // this.$store.dispatch(\"setLayout\", \"logged-in-layout\");\n          this.$cookie.set('rememberMe', true, 365)\n          // this.$router.push({name: 'Home'})\n          this.$router.push({name: 'FrontPage'});\n          document.querySelector('button.modal-close').click()\n        });\n      },\n      OnGoogleAuthFail (error) {\n        console.log(error)\n        this.googleIsLoading = false;\n      }\n    }\n    /*created(evt) {\n    console.log(evt)\n      evt.preventDefault()\n      axios.post(`http://localhost:3003/api/register`, this.form)\n      .then(response => {\n        // JSON responses are automatically parsed.\n        this.form = response.data\n      })\n      .catch(e => {\n        console.log(e)\n      })\n\n\n    },*/\n  }\n</script>\n\n\n<style scoped>\n\n  .avatar {\n    margin-top: -70px;\n    padding-bottom: 20px;\n  }\n\n  .avatar img {\n    padding: 1rem;\n    background: #fff;\n    border-radius: 50%;\n    -webkit-box-shadow: 0 2px 3px rgba(10, 10, 10, .1), 0 0 0 1px rgba(10, 10, 10, .1);\n    box-shadow: 0 2px 3px rgba(10, 10, 10, .1), 0 0 0 1px rgba(10, 10, 10, .1);\n  }\n\n  input {\n    font-weight: 300;\n  }\n\n  p {\n    font-weight: 700;\n  }\n\n  p.subtitle {\n    padding-top: 1rem;\n  }\n\n  a {\n    text-decoration: none;\n  }\n\n  .w-480 {\n    width: 480px;\n    margin-top: 100px\n  }\n\n</style>\n"]}]);

