x; } } }\n\n@if $variable-columns {\n  .columns.is-variable {\n    --columnGap: 0.75rem;\n    margin-left: calc(-1 * var(--columnGap));\n    margin-right: calc(-1 * var(--columnGap));\n    .column {\n      padding-left: var(--columnGap);\n      padding-right: var(--columnGap); }\n    @for $i from 0 through 8 {\n      &.is-#{$i} {\n        --columnGap: #{$i * 0.25rem}; } } } }\n",".tile {\n  align-items: stretch;\n  display: block;\n  flex-basis: 0;\n  flex-grow: 1;\n  flex-shrink: 1;\n  min-height: min-content;\n  // Modifiers\n  &.is-ancestor {\n    margin-left: -0.75rem;\n    margin-right: -0.75rem;\n    margin-top: -0.75rem;\n    &:last-child {\n      margin-bottom: -0.75rem; }\n    &:not(:last-child) {\n      margin-bottom: 0.75rem; } }\n  &.is-child {\n    margin: 0 !important; }\n  &.is-parent {\n    padding: 0.75rem; }\n  &.is-vertical {\n    flex-direction: column;\n    & > .tile.is-child:not(:last-child) {\n      margin-bottom: 1.5rem !important; } }\n  // Responsiveness\n  @include tablet {\n    &:not(.is-child) {\n      display: flex; }\n    @for $i from 1 through 12 {\n      &.is-#{$i} {\n        flex: none;\n        width: ($i / 12) * 100%; } } } }\n","// Main container\n\n.hero {\n  align-items: stretch;\n  display: flex;\n  flex-direction: column;\n  justify-content: space-between;\n  .navbar {\n    background: none; }\n  .tabs {\n    ul {\n      border-bottom: none; } }\n  // Colors\n  @each $name, $pair in $colors {\n    $color: nth($pair, 1);\n    $color-invert: nth($pair, 2);\n    &.is-#{$name} {\n      background-color: $color;\n      color: $color-invert;\n      a:not(.button):not(.dropdown-item):not(.tag),\n      strong {\n        color: inherit; }\n      .title {\n        color: $color-invert; }\n      .subtitle {\n        color: rgba($color-invert, 0.9);\n        a:not(.button),\n        strong {\n          color: $color-invert; } }\n      .navbar-menu {\n        @include touch {\n          background-color: $color; } }\n      .navbar-item,\n