      opacity: 1;\r\n    }\r\n    to {\r\n        opacity: 0;\r\n    }\r\n}\r\n.fadeOut {\r\n    animation-name: fadeOut;\r\n}\r\n\r\n@keyframes fadeOutDown {\r\n    from {\r\n        opacity: 1;\r\n    }\r\n    to {\r\n        opacity: 0;\r\n        transform: translate3d(0, 100%, 0);\r\n    }\r\n}\r\n.fadeOutDown {\r\n    animation-name: fadeOutDown;\r\n}\r\n\r\n@keyframes fadeOutUp {\r\n    from {\r\n        opacity: 1;\r\n    }\r\n    to {\r\n        opacity: 0;\r\n        transform: translate3d(0, -100%, 0);\r\n    }\r\n}\r\n.fadeOutUp {\r\n    animation-name: fadeOutUp;\r\n}\r\n\r\n// Fade In\r\n@keyframes fadeIn {\r\n    from {\r\n        opacity: 0;\r\n    }\r\n    to {\r\n        opacity: 1;\r\n    }\r\n}\r\n.fadeIn {\r\n    animation-name: fadeIn;\r\n}\r\n\r\n@keyframes fadeInDown {\r\n    from {\r\n        opacity: 0;\r\n        transform: translate3d(0, -100%, 0);\r\n    }\r\n    to {\r\n        opacity: 1;\r\n        transform: none;\r\n    }\r\n}\r\n.fadeInDown {\r\n    animation-name: fadeInDown;\r\n}\r\n\r\n@keyframes fadeInUp {\r\n    from {\r\n        opacity: 0;\r\n        transform: translate3d(0, 100%, 0);\r\n    }\r\n    to {\r\n        opacity: 1;\r\n        transform: none;\r\n    }\r\n}\r\n.fadeInUp {\r\n    animation-name: fadeInUp;\r\n}\r\n\r\n/**\r\n * Vue Transitions\r\n */\r\n\r\n// Fade\r\n.fade-enter-active,\r\n.fade-leave-active {\r\n    transition: opacity $speed-slow $easing;\r\n}\r\n.fade-enter,\r\n.fade-leave-to {\r\n    opacity: 0;\r\n}\r\n\r\n// Zoom In\r\n.zoom-in-enter-active,\r\n.zoom-in-leave-active {\r\n    transition: opacity $speed-slow $easing;\r\n    .animation-content,\r\n    .animation-content {\r\n        transition: transform $speed-slow $easing;\r\n    }\r\n}\r\n.zoom-in-enter,\r\n.zoom-in-leave-active {\r\n    opacity: 0;\r\n    .animation-content,\r\n    .animation-content {\r\n        transform: scale(0.95);\r\n    }\r\n}\r\n\r\n\r\n// Zoom Out\r\n.zoom-out-enter-active,\r\n.zoom-out-leave-active {\r\n    transition: opacity $speed-slow $easing;\r\n    .animation-content,\r\n    .animation-content {\r\n        transition: transform $speed-slow $easing;\r\n    }\r\n}\r\n.zoom-out-enter,\r\n.zoom-out-leave-active {\r\n    opacity: 0;\r\n    .animation-content,\r\n    .animation-content {\r\n        transform: scale(1.05);\r\n    }\r\n}\r\n\r\n// Slide\r\n.slide-next-enter-active,\r\n.slide-next-leave-active,\r\n.slide-prev-enter-active,\r\n.slide-prev-leave-active {\r\n    transition: transform $speed-slower cubic-bezier(0.785, 0.135, 0.150, 0.860);\r\n}\r\n.slide-prev-leave-to, .slide-next-enter {\r\n    transform: translate3d(-100%, 0, 0);\r\n    position: absolute;\r\n    width: 100%;\r\n}\r\n.slide-prev-enter, .slide-next-leave-to {\r\n    transform: translate3d(100%, 0, 0);\r\n    position: absolute;\r\n    width: 100%;\r\n}\r\n",".autocomplete {\r\n    position: relative;\r\n    .dropdown-menu {\r\n        display: block;\r\n        min-width: 100%;\r\n        max-width: 100%;\r\n        &.is-opened-top {\r\n            top: auto;\r\n            bottom: 100%;\r\n        }\r\n    }\r\n    .dropdown-content {\r\n        overflow: auto;\r\n        max-height: 200px;\r\n    }\r\n    .dropdown-item {\r\n        white-space: nowrap;\r\n        overflow: hidden;\r\n        text-overflow: ellipsis;\r\n        &.is-hovered {\r\n            background: $dropdown-item-hover-background-color;\r\n            color: $dropdown-item-hover-color;\r\n        }\r\n        &.is-disabled {\r\n            opacity: 0.5;\r\n            cursor: not-allowed;\r\n        }\r\n    }\r\n    &.is-small {\r\n        @include control-small;\r\n    }\r\n    &.is-medium{\r\n        @include control-medium;\r\n    }\r\n    &.is-large {\r\n        @include control-large;\r\n    }\r\n}\r\n","$checkbox-active-background-color: $primary !default;\r\n$checkbox-checkmark-color: $primary-invert !default;\r\n\r\n.b-checkbox {\r\n    &.checkbox {\r\n        outline: none;\r\n        display: inline-flex;\r\n        align-items: center;\r\n        @include unselectable;\r\n        & + .checkbox {\r\n            margin-left: 0.5em;\r\n        }\r\n        input[type=checkbox] {\r\n            position: absolute;\r\n            left: 0;\r\n            opacity: 0;\r\n            outline: none;\r\n            z-index: -1;\r\n            + .check {\r\n                width: 1.25em;\r\n                height: 1.25em;\r\n                flex-shrink: 0;\r\n                border-radius: $radius;\r\n                border: 2px solid $grey;\r\n                transition: background $speed-slow $easing;\r\n            }\r\n            &:checked + .check {\r\n                background: $checkbox-active-background-color url(checkmark($checkbox-checkmark-color)) no-repeat center center;\r\n                border-color: $checkbox-active-background-color;\r\n                @each $name, $pair in $colors {\r\n                    $color: nth($pair, 1);\r\n                    $color-invert: nth($pair, 2);\r\n                    &.is-#{$name} {\r\n                        background: $color url(checkmark($color-invert)) no-repeat center center;\r\n                        border-color: $color;\r\n                    }\r\n                }\r\n            }\r\n        }\r\n        .control-label {\r\n            padding-left: 0.5em;\r\n        }\r\n        &[disabled] {\r\n            opacity: 0.5;\r\n        }\r\n        &:hover {\r\n            input[type=checkbox] + .check {\r\n                border-color: $checkbox-active-background-color;\r\n                @each $name, $pair in $colors {\r\n                    $color: nth($pair, 1);\r\n                    &.is-#{$name} {\r\n                        border-color: $color;\r\n                    }\r\n                }\r\n            }\r\n        }\r\n        &:focus {\r\n            input[type=checkbox] + .check {\r\n                box-shadow: 0 0 0.5em rgba($grey, 0.8);\r\n            }\r\n            input[type=checkbox]:checked + .check {\r\n                box-shadow: 0 0 0.5em rgba($checkbox-active-background-color, 0.8);\r\n                @each $name, $pair in $colors {\r\n                    $color: nth($pair, 1);\r\n                    &.is-#{$name} {\r\n                        box-shadow: 0 0 0.5em rgba($color, 0.8);\r\n                    }\r\n                }\r\n            }\r\n        }\r\n        &.is-small {\r\n            @include control-small;\r\n        }\r\n        &.is-medium{\r\n            @include control-medium;\r\n        }\r\n        &.is-large {\r\n            @include control-large;\r\n        }\r\n    }\r\n}\r\n",".collapse {\r\n    .collapse-trigger {\r\n        display: inline;\r\n        cursor: pointer;\r\n    }\r\n    .collapse-content {\r\n        display: inherit;\r\n    }\r\n}\r\n",".datepicker {\r\n    font-size: 0.875rem;\r\n    .dropdown,\r\n    .dropdown-trigger {\r\n        width: 100%;\r\n    }\r\n    .dropdown-item {\r\n        font-size: inherit;\r\n    }\r\n\r\n    .datepicker-header {\r\n        padding-bottom: 0.875rem;\r\n        margin-bottom: 0.875rem;\r\n        border-bottom: 1px solid $grey-lighter;\r\n    }\r\n    .datepicker-footer {\r\n        padding-top: 0.875rem;\r\n        border-top: 1px solid $grey-lighter;\r\n    }\r\n\r\n    .datepicker-table {\r\n        display: table;\r\n        margin: 0 auto 0.875rem auto;\r\n        .datepicker-cell {\r\n            text-align: center;\r\n            vertical-align: middle;\r\n            display: table-cell;\r\n            border-radius: $radius;\r\n            padding: 0.5rem 0.75rem;\r\n        }\r\n        .datepicker-header {\r\n            display: table-header-group;\r\n            .datepicker-cell {\r\n                color: $grey;\r\n                font-weight: $weight-semibold;\r\n            }\r\n        }\r\n        .datepicker-body {\r\n            display: table-row-group;\r\n            .datepicker-row {\r\n                display: table-row;\r\n                .datepicker-cell {\r\n                    &.is-unselectable {\r\n                        color: $grey-light;\r\n                    }\r\n                    &.is-today {\r\n                        border: solid 1px rgba($primary, 0.5);\r\n                    }\r\n                    &.is-selectable {\r\n                        color: $grey-dark;\r\n                        &:hover:not(.is-selected),\r\n                        &:focus:not(.is-selected) {\r\n                            background-color: $background;\r\n                            color: $black;\r\n                            cursor: pointer;\r\n                        }\r\n                    }\r\n                    &.is-selected {\r\n                        background-color: $primary;\r\n                        color: $primary-invert;\r\n                    }\r\n                }\r\n            }\r\n            &.has-events {\r\n                .datepicker-cell {\r\n                    padding: 0.3rem 0.75rem 0.75rem;\r\n                    &.has-event {\r\n                        position: relative;\r\n                        .events {\r\n                            bottom: .425rem;\r\n                            display: flex;\r\n                            justify-content: center;\r\n                            left: 0;\r\n                            padding: 0 .35rem;\r\n                            position: absolute;\r\n                            width: 100%;\r\n                            .event {\r\n                                @each $name, $pair in $colors {\r\n                                    $color: nth($pair, 1);\r\n                                    &.is-#{$name} {\r\n                                        background-color: $color;\r\n                                    }\r\n                                }\r\n                            }\r\n                        }\r\n                        &.dots .event {\r\n                            border-radius: 50%;\r\n                            height: .35em;\r\n                            margin: 0 .1em;\r\n                            width: .35em;\r\n                        }\r\n                        &.bars .event {\r\n                            height: .25em;\r\n                            width: 100%;\r\n                        }\r\n                    }\r\n                    &.is-selected {\r\n                        overflow: hidden;\r\n                        .events .event {\r\n                            // Currently datepicker only uses primary coloring\r\n                            // Ensure indicator is visible when selected\r\n                            &.is-primary {\r\n                                background-color: lighten($primary,15);\r\n                            }\r\n                        }\r\n                    }\r\n                }\r\n            }\r\n        }\r\n    }\r\n    &.is-small {\r\n        @include control-small;\r\n    }\r\n    &.is-medium {\r\n        @include control-medium;\r\n    }\r\n    &.is-large {\r\n        @include control-large;\r\n    }\r\n    @include tablet-only {\r\n        .datepicker-table {\r\n            .datepicker-cell {\r\n                padding: 0.75rem 1rem;\r\n            }\r\n        }\r\n    }\r\n    @include mobile {\r\n        .datepicker-table {\r\n            .datepicker-cell {\r\n                padding: 0.25rem 0.5rem;\r\n            }\r\n        }\r\n    }\r\n}\r\n",".dialog {\r\n    .modal-card {\r\n        max-width: 460px;\r\n        width: auto;\r\n        .modal-card-head {\r\n            font-size: $size-5;\r\n            font-weight: $weight-semibold;\r\n        }\r\n        .modal-card-body {\r\n            .field {\r\n                margin-top: 16px;\r\n            }\r\n            &.is-titleless {\r\n                border-top-left-radius: $radius-large;\r\n                border-top-right-radius: $radius-large;\r\n            }\r\n        }\r\n        .modal-card-foot {\r\n            justify-content: flex-end;\r\n            .button {\r\n                display: inline; // Fix Safari centering\r\n                min-width: 5em;\r\n                font-weight: $weight-semibold;\r\n            }\r\n        }\r\n        @include tablet {\r\n            min-width: 320px;\r\n        }\r\n    }\r\n    &.is-small {\r\n        .modal-card,\r\n        .input,\r\n        .button {\r\n            @include control-small;\r\n        }\r\n    }\r\n    &.is-medium {\r\n        .modal-card,\r\n        .input,\r\n        .button {\r\n            @include control-medium;\r\n        }\r\n    }\r\n    &.is-large {\r\n        .modal-card,\r\n        .input,\r\n        .button {\r\n            @include control-large;\r\n        }\r\n    }\r\n}\r\n",".dropdown {\r\n    & + .dropdown {\r\n        margin-left: 0.5em;\r\n    }\r\n    .background {\r\n        @include overlay;\r\n        position: fixed;\r\n        background-color: rgba($black, 0.86);\r\n        z-index: 10;\r\n        cursor: pointer;\r\n        @include desktop {\r\n            display: none;\r\n        }\r\n    }\r\n    .dropdown-menu {\r\n        .dropdown-item {\r\n            &.is-disabled {\r\n                cursor: not-allowed;\r\n                &:hover {\r\n                    background: inherit;\r\n                    color: inherit;\r\n                }\r\n            }\r\n        }\r\n        .has-link a {\r\n            @extend .dropdown-item;\r\n            padding-right: 3rem;\r\n            white-space: nowrap;\r\n        }\r\n    }\r\n\r\n    &:not(.is-disabled) {\r\n        .dropdown-menu {\r\n            .dropdown-item {\r\n                &.is-disabled {\r\n                    opacity: 0.5;\r\n                }\r\n            }\r\n        }\r\n    }\r\n    .navbar-item {\r\n        height: 100%;\r\n    }\r\n    &.is-disabled {\r\n        opacity: 0.5;\r\n        cursor: not-allowed;\r\n        .dropdown-trigger {\r\n            pointer-events: none;\r\n        }\r\n    }\r\n    &.is-inline {\r\n        .dropdown-menu {\r\n            position: static;\r\n            display: inline-block;\r\n            padding: 0;\r\n        }\r\n    }\r\n    &.is-top-right {\r\n        .dropdown-menu {\r\n            top: auto;\r\n            bottom: 100%;\r\n        }\r\n    }\r\n    &.is-top-left {\r\n        .dropdown-menu {\r\n            top: auto;\r\n            bottom: 100%;\r\n            right: 0;\r\n            left: auto;\r\n        }\r\n    }\r\n    &.is-bottom-left {\r\n        .dropdown-menu {\r\n            right: 0;\r\n            left: auto;\r\n        }\r\n    }\r\n    @include touch {\r\n        &.is-mobile-modal {\r\n            .dropdown-menu {\r\n                position: fixed;\r\n                width: calc(100vw - 40px);\r\n                max-width: 460px;\r\n                max-height: calc(100vh - 120px);\r\n                top: 25% !important;\r\n                left: 50% !important;\r\n                bottom: auto !important;\r\n                right: auto !important;\r\n                transform: translate3d(-50%, -25%, 0);\r\n                white-space: normal;\r\n                overflow-y: auto;\r\n                .dropdown-item {\r\n                    padding: 1rem 1.5rem;\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n",".label {\r\n    font-weight: $weight-semibold\r\n}\r\n\r\n.field {\r\n    &.is-grouped {\r\n        .field {\r\n            flex-shrink: 0;\r\n            &+.field {\r\n                margin-left: 0.75rem;\r\n            }\r\n            &.is-expanded {\r\n                flex-grow: 1;\r\n                flex-shrink: 1;\r\n            }\r\n        }\r\n    }\r\n    // Nested control addons (for Autocomplete and Datepicker)\r\n    &.has-addons .control {\r\n        &:first-child .control {\r\n            .button,\r\n            .input,\r\n            .select select {\r\n                border-bottom-left-radius: $input-radius;\r\n                border-top-left-radius: $input-radius;\r\n            }\r\n        }\r\n        &:last-child .control {\r\n            .button,\r\n            .input,\r\n            .select select {\r\n                border-bottom-right-radius: $input-radius;\r\n                border-top-right-radius: $input-radius;\r\n            }\r\n        }\r\n        .control {\r\n            .button,\r\n            .input,\r\n            .select select {\r\n                border-radius: 0\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n.control {\r\n    .help.counter {\r\n        float: right;\r\n        margin-left: 0.5em;\r\n    }\r\n    .icon {\r\n        &.is-clickable {\r\n            pointer-events: auto;\r\n            cursor: pointer;\r\n        }\r\n    }\r\n}\r\n",".icon {\r\n    @include unselectable;\r\n    cursor: inherit;\r\n    svg {\r\n        background-color: transparent;\r\n        fill: currentColor;\r\n        stroke-width: 0;\r\n        stroke: currentColor;\r\n        pointer-events: none;\r\n        width: 1.5rem;\r\n        height: 1.5rem;\r\n    }\r\n}\r\n","\r\n.loading-overlay {\r\n    @include overlay;\r\n    align-items: center;\r\n    display: none;\r\n    justify-content: center;\r\n    overflow: hidden;\r\n    &.is-active {\r\n        display: flex\r\n    }\r\n    &.is-full-page {\r\n        z-index: 999;\r\n        position: fixed;\r\n        .loading-icon {\r\n            &:after {\r\n                top: calc(50% - 2.5em);\r\n                left: calc(50% - 2.5em);\r\n                width: 5em;\r\n                height: 5em;\r\n            }\r\n        }\r\n    }\r\n    .loading-background {\r\n        @include overlay;\r\n        background:#7f7f7f;\r\n        background:rgba(255,255,255,0.5);\r\n    }\r\n    .loading-icon {\r\n        position: relative;\r\n        &:after {\r\n            @include loader;\r\n            position: absolute;\r\n            top: calc(50% - 1.5em);\r\n            left: calc(50% - 1.5em);\r\n            width: 3em;\r\n            height: 3em;\r\n            border-width: 0.25em;\r\n        }\r\n    }\r\n}\r\n",".message,\r\n.notification {\r\n    .media {\r\n        padding-top: 0;\r\n        border: 0;\r\n    }\r\n}\r\n\r\n// Fix for Bulma 0.6.1\r\n.notification > .delete {\r\n    right: 0.5rem !important;\r\n    top: 0.5rem !important;\r\n}\r\n",".modal {\r\n    .animation-content {\r\n        margin: 0 20px;\r\n        .modal-card {\r\n            margin: 0;\r\n        }\r\n        @include mobile {\r\n            width: 100%;\r\n        }\r\n    }\r\n}\r\n","$snackbar-background-color: $dark !default;\r\n\r\n.notices {\r\n    position: fixed;\r\n    display: flex;\r\n    top: 0;\r\n    bottom: 0;\r\n    left: 0;\r\n    right: 0;\r\n    padding: 2em;\r\n    overflow: hidden;\r\n    z-index: 1000;\r\n    pointer-events: none;\r\n    .toast {\r\n        display: inline-flex;\r\n        animation-duration: $speed-slow;\r\n        margin: 0.5em 0;\r\n        text-align: center;\r\n        box-shadow: 0 1px 4px rgba(0, 0, 0, 0.12), 0 0 6px rgba(0, 0, 0, 0.04);\r\n        border-radius: 2em;\r\n        padding: 0.75em 1.5em;\r\n        pointer-events: auto;\r\n        opacity: 0.92;\r\n        @each $name, $pair in $colors {\r\n            $color: nth($pair, 1);\r\n            $color-invert: nth($pair, 2);\r\n            &.is-#{$name} {\r\n                color: $color-invert;\r\n                background: $color;\r\n            }\r\n        }\r\n    }\r\n    .snackbar {\r\n        display: inline-flex;\r\n        align-items: center;\r\n        justify-content: space-around;\r\n        animation-duration: $speed-slow;\r\n        margin: 0.5em 0;\r\n        box-shadow: 0 1px 4px rgba(0, 0, 0, 0.12), 0 0 6px rgba(0, 0, 0, 0.04);\r\n        border-radius: $radius;\r\n        pointer-events: auto;\r\n        background: $snackbar-background-color;\r\n        color: $dark-invert;\r\n        min-height: 3em;\r\n        .text {\r\n            padding: 0.5em 1em;\r\n        }\r\n        .action {\r\n            margin-left: auto;\r\n            padding: 0.5em;\r\n            padding-left: 0;\r\n            .button {\r\n                font-weight: $weight-semibold;\r\n                text-transform: uppercase;\r\n            }\r\n            @each $name, $pair in $colors {\r\n                $color: nth($pair, 1);\r\n                &.is-#{$name} {\r\n                    .button {\r\n                        color: $color;\r\n                    }\r\n                }\r\n            }\r\n        }\r\n        @include mobile {\r\n            width: 100%;\r\n            margin: 0;\r\n            border-radius: 0;\r\n        }\r\n        @include tablet {\r\n            min-width: 350px;\r\n            max-width: 600px;\r\n            overflow: hidden;\r\n        }\r\n    }\r\n\r\n    .toast,\r\n    .snackbar {\r\n        // Modifiers\r\n        &.is-top, &.is-bottom {\r\n            align-self: center;\r\n        }\r\n        &.is-top-right, &.is-bottom-right {\r\n            align-self: flex-end;\r\n        }\r\n        &.is-top-left, &.is-bottom-left {\r\n            align-self: flex-start;\r\n        }\r\n        &.is-toast {\r\n            opacity: 0.92;\r\n        }\r\n    }\r\n\r\n    // Modifiers\r\n    &.is-top {\r\n        flex-direction: column;\r\n    }\r\n    &.is-bottom {\r\n        flex-direction: column-reverse;\r\n    }\r\n    &.has-custom-container {\r\n        position: absolute;\r\n    }\r\n\r\n    @include mobile {\r\n        padding: 0;\r\n        position: fixed !important;\r\n    }\r\n}\r\n","$speed-slow: 150ms !default;\r\n$speed-slower: 250ms !default;\r\n\r\n$primary: #7957d5 !default;\r\n$primary-invert: $white !default;\r\n\r\n$info: #167df0;\r\n\r\n$link: $primary !default;\r\n$link-invert: $primary-invert !default;\r\n$link-visited: $grey !default;\r\n$link-focus-border: $primary !default;\r\n",".pagination {\r\n    .pagination-next,\r\n    .pagination-previous {\r\n        padding-left: 0.25em;\r\n        padding-right: 0.25em;\r\n        &.is-disabled {\r\n            pointer-events: none;\r\n            cursor: not-allowed;\r\n            opacity: 0.5;\r\n        }\r\n    }\r\n    &.is-simple {\r\n        justify-content: normal;\r\n    }\r\n    .is-current {\r\n        pointer-events: none;\r\n        cursor: not-allowed;\r\n    }\r\n}\r\n",".panel {\r\n    .panel-heading {\r\n        &.is-collapsible {\r\n            cursor: pointer;\r\n        }\r\n    }\r\n    .panel-content {\r\n        width: 100%;\r\n    }\r\n}\r\n","$radio-active-background-color: $primary !default;\r\n\r\n.b-radio {\r\n    &.radio {\r\n        outline: none;\r\n        display: inline-flex;\r\n        align-items: center;\r\n        @include unselectable;\r\n        & + .radio {\r\n            margin-left: 0.5em;\r\n        }\r\n        input[type=radio] {\r\n            position: absolute;\r\n            left: 0;\r\n            opacity: 0;\r\n            outline: none;\r\n            z-index: -1;\r\n            + .check {\r\n                display: flex;\r\n                align-items: center;\r\n                justify-content: center;\r\n                width: 1.25em;\r\n                height: 1.25em;\r\n                border: 2px solid $grey;\r\n                border-radius: 50%;\r\n                transition: background $speed-slow $easing;\r\n                &:before {\r\n                    content: \"\";\r\n                    border-radius: 50%;\r\n                    width: 0.625em;\r\n                    height: 0.625em;\r\n                    background: $radio-active-background-color;\r\n                    transform: scale(0);\r\n                    transition: transform $speed-slow $easing;\r\n                }\r\n                @each $name, $pair in $colors {\r\n                    $color: nth($pair, 1);\r\n                    &.is-#{$name}:before {\r\n                        background: $color;\r\n                    }\r\n                }\r\n            }\r\n            &:checked + .check {\r\n                border-color: $radio-active-background-color;\r\n                @each $name, $pair in $colors {\r\n                    $color: nth($pair, 1);\r\n                    &.is-#{$name} {\r\n                        border-color: $color;\r\n                    }\r\n                }\r\n                &:before {\r\n                    transform: scale(1);\r\n                }\r\n            }\r\n        }\r\n        .control-label {\r\n            padding-left: 0.5em;\r\n        }\r\n        &[disabled] {\r\n            opacity: 0.5;\r\n        }\r\n        &:hover {\r\n            input[type=radio] + .check {\r\n                border-color: $radio-active-background-color;\r\n                @each $name, $pair in $colors {\r\n                    $color: nth($pair, 1);\r\n                    &.is-#{$name} {\r\n                        border-color: $color;\r\n                    }\r\n                }\r\n            }\r\n        }\r\n        &:focus {\r\n            input[type=radio] + .check {\r\n                box-shadow: 0 0 0.5em rgba($grey, 0.8);\r\n            }\r\n            input[type=radio]:checked + .check {\r\n                box-shadow: 0 0 0.5em rgba($radio-active-background-color, 0.8);\r\n                @each $name, $pair in $colors {\r\n                    $color: nth($pair, 1);\r\n                    &.is-#{$name} {\r\n                        box-shadow: 0 0 0.5em rgba($color, 0.8);\r\n                    }\r\n                }\r\n            }\r\n        }\r\n        &.is-small {\r\n            @include control-small;\r\n        }\r\n        &.is-medium{\r\n            @include control-medium;\r\n        }\r\n        &.is-large {\r\n            @include control-large;\r\n        }\r\n    }\r\n}\r\n",".select {\r\n    select {\r\n        padding-right: 2.5em;\r\n        option {\r\n            color: $grey-dark;\r\n            padding: 0.25em 0.5em;\r\n        }\r\n        option:disabled {\r\n            cursor: not-allowed;\r\n            opacity: 0.5;\r\n        }\r\n        optgroup {\r\n            color: $grey-light;\r\n            font-weight: $weight-normal;\r\n            font-style: normal;\r\n            padding: 0.25em 0;\r\n        }\r\n    }\r\n    &.is-empty select {\r\n        color: rgba($grey, 0.7);\r\n    }\r\n\r\n}\r\n","$switch-width-number: 2.75;\r\n$switch-width: $switch-width-number * 1em;\r\n$switch-padding: 0.2em;\r\n\r\n$switch-active-background-color: $primary !default;\r\n\r\n.switch {\r\n    cursor: pointer;\r\n    display: inline-flex;\r\n    align-items: center;\r\n    @include unselectable;\r\n    & + .switch {\r\n        margin-left: 0.5em;\r\n    }\r\n    input[type=checkbox] {\r\n        display: none;\r\n        + .check {\r\n            display: flex;\r\n            align-items: center;\r\n            flex-shrink: 0;\r\n            width: $switch-width;\r\n            height: #{$switch-width / 2 + $switch-padding};\r\n            padding: $switch-padding;\r\n            background: $grey-light;\r\n            border-radius: 1em;\r\n            transition: background $speed-slow $easing;\r\n            &:before {\r\n                content: \"\";\r\n                border-radius: 1em;\r\n                width: #{($switch-width - $switch-padding * 2) / 2};\r\n                height: #{($switch-width - $switch-padding * 2) / 2};\r\n                background: $background;\r\n                box-shadow: 0 3px 1px 0 rgba(0, 0, 0, 0.05), 0 2px 2px 0 rgba(0, 0, 0, 0.1), 0 3px 3px 0 rgba(0, 0, 0, 0.05);\r\n                transition: transform $speed-slow $easing, width $speed-slow $easing;\r\n                will-change: transform;\r\n            }\r\n            &.is-elastic:before {\r\n                width: 1.75em;\r\n            }\r\n        }\r\n        &:checked + .check {\r\n            background: $switch-active-background-color;\r\n            @each $name, $pair in $colors {\r\n                $color: nth($pair, 1);\r\n                &.is-#{$name} {\r\n                    background: $color;\r\n                }\r\n            }\r\n            &:before {\r\n                transform: translate3d(100%, 0, 0);\r\n            }\r\n            &.is-elastic:before {\r\n                // Might be a little offset if base font is not 16px\r\n                transform: translate3d(percentage(16 / ($switch-width-number * 16)), 0, 0);\r\n            }\r\n        }\r\n    }\r\n    .control-label {\r\n        padding-left: 0.5em;\r\n    }\r\n    &:hover {\r\n        input[type=checkbox] + .check {\r\n            background: rgba($grey-light, 0.9);\r\n        }\r\n        input[type=checkbox]:checked + .check {\r\n            background: rgba($switch-active-background-color, 0.9);\r\n            @each $name, $pair in $colors {\r\n                $color: nth($pair, 1);\r\n                &.is-#{$name} {\r\n                    background: rgba($color, 0.9);\r\n                }\r\n            }\r\n        }\r\n    }\r\n    &:focus {\r\n        outline: none;\r\n        input[type=checkbox] + .check {\r\n            box-shadow: 0 0 0.5em rgba($grey, 0.6);\r\n        }\r\n        input[type=checkbox]:checked + .check {\r\n            box-shadow: 0 0 0.5em rgba($switch-active-background-color, 0.8);\r\n            @each $name, $pair in $colors {\r\n                $color: nth($pair, 1);\r\n                &.is-#{$name} {\r\n                    box-shadow: 0 0 0.5em rgba($color, 0.8);\r\n                }\r\n            }\r\n        }\r\n    }\r\n    &.is-small {\r\n        @include control-small;\r\n    }\r\n    &.is-medium{\r\n        @include control-medium;\r\n    }\r\n    &.is-large {\r\n        @include control-large;\r\n    }\r\n    &[disabled] {\r\n        opacity: 0.5;\r\n        cursor: not-allowed;\r\n        color: $grey;\r\n    }\r\n}\r\n",".table-wrapper {\r\n    .table {\r\n        margin-bottom: 0;\r\n    }\r\n    &:not(:last-child) {\r\n        margin-bottom: 1.5rem;\r\n    }\r\n    @include touch {\r\n        overflow-x: auto;\r\n    }\r\n}\r\n\r\n.b-table {\r\n    transition: opacity $speed $easing;\r\n\r\n    .table-mobile-sort {\r\n        @include tablet {\r\n            display: none;\r\n        }\r\n    }\r\n\r\n    .icon {\r\n        transition: transform $speed-slow $easing, opacity $speed $easing;\r\n        &.is-desc {\r\n            transform: rotate(180deg);\r\n        }\r\n        &.is-expanded {\r\n            transform: rotate(90deg);\r\n        }\r\n    }\r\n\r\n    .table {\r\n        width: 100%;\r\n        border: 1px solid transparent;\r\n        border-radius: $radius;\r\n        border-collapse: separate;\r\n        th {\r\n            font-weight: $weight-semibold;\r\n            .th-wrap {\r\n                display: flex;\r\n                align-items: center;\r\n                .icon {\r\n                    margin-left: 0.5rem;\r\n                    margin-right: 0;\r\n                    font-size: 1rem;\r\n                }\r\n                &.is-numeric {\r\n                    flex-direction: row-reverse;\r\n                    text-align: right;\r\n                    .icon {\r\n                        margin-left: 0;\r\n                        margin-right: 0.5rem;\r\n                    }\r\n                }\r\n                &.is-centered {\r\n                    justify-content: center;\r\n                    text-align: center;\r\n                }\r\n            }\r\n            &.is-current-sort {\r\n                border-color: $grey;\r\n                font-weight: $weight-bold;\r\n            }\r\n            &.is-sortable:hover {\r\n                border-color: $grey;\r\n            }\r\n            &.is-sortable,\r\n            &.is-sortable .th-wrap {\r\n                cursor: pointer;\r\n            }\r\n        }\r\n        tr {\r\n            &.is-selected {\r\n                .checkbox input {\r\n                    &:checked + .check {\r\n                        background: $table-row-active-color url(checkmark($table-row-active-background-color)) no-repeat center center;\r\n                    }\r\n                    + .check {\r\n                        border-color: $table-row-active-color;\r\n                    }\r\n                }\r\n            }\r\n            &.is-empty:hover {\r\n                background-color: transparent;\r\n            }\r\n        }\r\n        .chevron-cell {\r\n          vertical-align:middle\r\n        }\r\n        .checkbox-cell {\r\n            width: 40px;\r\n            .checkbox {\r\n                vertical-align: middle;\r\n                .check {\r\n                    transition: none;\r\n                }\r\n            }\r\n        }\r\n        tr.detail {\r\n            box-shadow: inset 0 1px 3px $grey-lighter;\r\n            background: $white-bis;\r\n            .detail-container {\r\n                padding: 1rem;\r\n            }\r\n        }\r\n        &:focus {\r\n            border-color: $link;\r\n            box-shadow: 0 0 0 0.125em rgba($link, 0.25);\r\n        }\r\n\r\n        // Modifiers\r\n        &.is-bordered {\r\n            th.is-current-sort,\r\n            th.is-sortable:hover {\r\n                border-color: $grey-lighter;\r\n                background: $background;\r\n            }\r\n        }\r\n        &.has-mobile-cards {\r\n            @include mobile {\r\n                thead {\r\n                    display: none;\r\n                }\r\n                tfoot {\r\n                     th {\r\n                        border: 0; // Disable is-bordered\r\n                        display: inherit;\r\n                     }\r\n                }\r\n                tr {\r\n                    // Card style  Cannot extend inside media query\r\n                    box-shadow: 0 2px 3px rgba($black, 0.1), 0 0 0 1px rgba($black, 0.1);\r\n                    max-width: 100%;\r\n                    position: relative;\r\n                    display: block;\r\n                    td {\r\n                        border: 0; // Disable is-bordered\r\n                        display: inherit;\r\n                        &:last-child {\r\n                            border-bottom: 0;\r\n                        }\r\n                    }\r\n                    &:not(:last-child) {\r\n                        margin-bottom: 1rem;\r\n                    }\r\n                    // Disables is-*\r\n                    &:not([class*=\"is-\"]) {\r\n                        background: inherit;\r\n                        &:hover {\r\n                            background-color: inherit;\r\n                        }\r\n                    }\r\n                    &.detail {\r\n                        margin-top: -1rem;\r\n                    }\r\n                }\r\n                tr:not(.detail):not(.is-empty):not(.table-footer) {\r\n                    td {\r\n                        display: flex;\r\n                        width: auto;\r\n                        justify-content: space-between;\r\n                        text-align: right;\r\n                        border-bottom: 1px solid $background;\r\n                        &:before {\r\n                            content: attr(data-label);\r\n                            font-weight: $weight-semibold;\r\n                            padding-right: 0.5em;\r\n                            text-align: left;\r\n                        }\r\n                    }\r\n                }\r\n            }\r\n        }\r\n    }\r\n\r\n    .level {\r\n        padding-bottom: 1.5rem;\r\n    }\r\n\r\n    &.is-loading {\r\n        position: relative;\r\n        pointer-events: none;\r\n        opacity: 0.5;\r\n        &:after {\r\n            @include loader;\r\n            position: absolute;\r\n            top: 4em;\r\n            left: calc(50% - 2.5em);\r\n            width: 5em;\r\n            height: 5em;\r\n            border-width: 0.25em;\r\n        }\r\n    }\r\n}\r\n",".b-tabs {\r\n    .tabs {\r\n        margin-bottom: 0;\r\n        flex-shrink: 0;\r\n    }\r\n    .is-disabled {\r\n        pointer-events: none;\r\n        cursor: not-allowed;\r\n        opacity: 0.5;\r\n    }\r\n    .tab-content {\r\n        position: relative;\r\n        overflow: hidden;\r\n        display: flex;\r\n        flex-direction: column;\r\n        padding: 1rem;\r\n        .tab-item {\r\n            flex-shrink: 0;\r\n            flex-basis: auto;\r\n        }\r\n    }\r\n    &:not(:last-child) {\r\n        margin-bottom: 1.5rem;\r\n    }\r\n    &.is-fullwidth {\r\n        width: 100%;\r\n    }\r\n}\r\n",".tag {\r\n    .has-ellipsis {\r\n        max-width: 10em;\r\n        overflow: hidden;\r\n        white-space: nowrap;\r\n        text-overflow: ellipsis;\r\n    }\r\n}\r\n","$taginput-height: 1.7em;\r\n\r\n.taginput {\r\n    .taginput-container {\r\n        &.is-focusable {\r\n            @extend .input;\r\n            padding-bottom: 0;\r\n            padding-top: calc(0.275em - 1px);\r\n            align-items: center;\r\n            display: flex;\r\n            flex-wrap: wrap;\r\n            justify-content: flex-start;\r\n            height: auto;\r\n            cursor: text;\r\n        }\r\n\r\n        > .tag,\r\n        > .tags {\r\n            margin-bottom: calc(0.275em - 1px);\r\n            font-size: 0.9em;\r\n            height: $taginput-height;\r\n            .tag {\r\n                margin-bottom: 0;\r\n                font-size: 0.9em;\r\n                height: $taginput-height;\r\n            }\r\n            &:not(:last-child) {\r\n                margin-right: 0.275rem;\r\n            }\r\n        }\r\n\r\n        .autocomplete {\r\n            flex: 1;\r\n            input {\r\n                height: $taginput-height;\r\n                margin-bottom: calc(0.275em - 1px);\r\n                padding-top: 0;\r\n                padding-bottom: 0;\r\n                border: none;\r\n                box-shadow: none;\r\n                min-width: 8em;\r\n                &:focus {\r\n                    box-shadow: none !important;\r\n                }\r\n            }\r\n            .icon {\r\n                height: $taginput-height;\r\n            }\r\n            > .control.is-loading::after {\r\n                top: 0.375em;\r\n            }\r\n        }\r\n    }\r\n}\r\n",".timepicker {\r\n    .dropdown,\r\n    .dropdown-trigger {\r\n        width: 100%;\r\n    }\r\n    .dropdown-item {\r\n        font-size: inherit;\r\n    }\r\n    .timepicker-footer {\r\n        padding-top: 0.875rem;\r\n    }\r\n    .control {\r\n        .select {\r\n            font-size: 2.5rem;\r\n            select {\r\n                font-weight: $weight-semibold;\r\n                padding-right: $control-padding-horizontal;\r\n                option {\r\n                    font-size: 1.5rem;\r\n                    &:disabled {\r\n                        color: rgba($grey, 0.7);\r\n                    }\r\n                }\r\n            }\r\n            &:after {\r\n                display: none;\r\n            }\r\n        }\r\n        &:nth-child(1) .select {\r\n            select {\r\n                border-right: none;\r\n            }\r\n            &:before {\r\n                content: \":\";\r\n                color: $grey-light;\r\n                display: inline-block;\r\n                position: absolute;\r\n                z-index: 6;\r\n                font-size: 3rem;\r\n                right: -0.325rem;\r\n                top: 0.25rem;\r\n            }\r\n        }\r\n        &:nth-child(2) .select select {\r\n            border-left: none;\r\n        }\r\n    }\r\n    // Sizes\r\n    &.is-small {\r\n        @include control-small;\r\n    }\r\n    &.is-medium {\r\n        @include control-medium;\r\n    }\r\n    &.is-large {\r\n        @include control-large;\r\n    }\r\n}\r\n","$tooltip-arrow-size: 5px;\r\n$tooltip-arrow-margin: 2px;\r\n\r\n$tooltip-multiline-sizes: (\r\n    small: 180px,\r\n    medium: 240px,\r\n    large: 300px\r\n);\r\n\r\n@mixin tooltip-arrow($direction, $color) {\r\n    @if ($direction == \"is-top\") {\r\n        border-top: $tooltip-arrow-size solid $color;\r\n        border-right: $tooltip-arrow-size solid transparent;\r\n        border-left: $tooltip-arrow-size solid transparent;\r\n        bottom: calc(100% + #{$tooltip-arrow-margin});\r\n    } @else if ($direction == \"is-bottom\") {\r\n        border-right: $tooltip-arrow-size solid transparent;\r\n        border-bottom: $tooltip-arrow-size solid $color;\r\n        border-left: $tooltip-arrow-size solid transparent;\r\n        top: calc(100% + #{$tooltip-arrow-margin});\r\n    } @else if ($direction == \"is-right\") {\r\n        border-top: $tooltip-arrow-size solid transparent;\r\n        border-right: $tooltip-arrow-size solid $color;\r\n        border-bottom: $tooltip-arrow-size solid transparent;\r\n        left: calc(100% + #{$tooltip-arrow-margin});\r\n    } @else if ($direction == \"is-left\") {\r\n        border-top: $tooltip-arrow-size solid transparent;\r\n        border-bottom: $tooltip-arrow-size solid transparent;\r\n        border-left: $tooltip-arrow-size solid $color;\r\n        right: calc(100% + #{$tooltip-arrow-margin});\r\n    }\r\n}\r\n\r\n@mixin tooltip($direction) {\r\n    &.#{$direction} {\r\n        &:before,\r\n        &:after {\r\n            @if ($direction == \"is-top\") {\r\n                top: auto;\r\n                right: auto;\r\n                bottom: calc(100% + #{$tooltip-arrow-size} + #{$tooltip-arrow-margin});\r\n                left: 50%;\r\n                transform: translateX(-50%);\r\n            } @else if ($direction == \"is-bottom\") {\r\n                top: calc(100% + #{$tooltip-arrow-size} + #{$tooltip-arrow-margin});\r\n                right: auto;\r\n                bottom: auto;\r\n                left: 50%;\r\n                transform: translateX(-50%);\r\n            } @else if ($direction == \"is-right\") {\r\n                top: 50%;\r\n                right: auto;\r\n                bottom: auto;\r\n                left: calc(100% + #{$tooltip-arrow-size} + #{$tooltip-arrow-margin});\r\n                transform: translateY(-50%);\r\n            } @else if ($direction == \"is-left\") {\r\n                top: 50%;\r\n                right: calc(100% + #{$tooltip-arrow-size} + #{$tooltip-arrow-margin});\r\n                bottom: auto;\r\n                left: auto;\r\n                transform: translateY(-50%);\r\n            }\r\n        }\r\n        @each $name, $pair in $colors {\r\n            $color: nth($pair, 1);\r\n            &.is-#{$name}:before {\r\n                @include tooltip-arrow($direction, $color)\r\n            }\r\n        }\r\n        &.is-multiline {\r\n            @each $name, $size in $tooltip-multiline-sizes {\r\n                &.is-#{$name}:after {\r\n                    width: $size;\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n// Base\r\n.tooltip {\r\n    @include tooltip(\"is-top\");\r\n    @include tooltip(\"is-right\");\r\n    @include tooltip(\"is-bottom\");\r\n    @include tooltip(\"is-left\");\r\n    position: relative;\r\n    display: inline-flex;\r\n    &:before,\r\n    &:after {\r\n        position: absolute;\r\n        content: \"\";\r\n        opacity: 0;\r\n        visibility: hidden;\r\n        pointer-events: none;\r\n    }\r\n    &:before {\r\n        z-index: 889;\r\n    }\r\n    &:after {\r\n        content: attr(data-label);\r\n        width: auto;\r\n        padding: 0.35rem 0.75rem;\r\n        border-radius: $radius-large;\r\n        font-size: 0.85rem;\r\n        font-weight: $weight-normal;\r\n        box-shadow: 0px 1px 2px 1px rgba(0, 1, 0, 0.2);\r\n        z-index: 888;\r\n        white-space: nowrap;\r\n    }\r\n    &:not([data-label=\"\"]):hover:before,\r\n    &:not([data-label=\"\"]):hover:after {\r\n        opacity: 1;\r\n        visibility: visible;\r\n    }\r\n    // Modifiers\r\n    @each $name, $pair in $colors {\r\n        $color: nth($pair, 1);\r\n        $color-invert: nth($pair, 2);\r\n        &.is-#