<template>
  <div class="container w-480">
    <gdpr-consent
      v-if="!gdprConsented"
      v-model="gdprConsented"
    />
    <div
      v-else
      class="column is-12"
    >
      <!--<h3 class="title has-text-grey">Sign up</h3>-->
      <p class="subtitle has-text-grey">.</p>
      <div class="box">
        <form>
          <div
            v-google-signin-button="googleSignInParams.client_id"
            class="button is-primary is-medium is-fullwidth google-signin-button"
            @click.native="googleIsLoading = true">
            Sign up with Google
          </div>

          <p class="has-text-centered my-2">
            or
          </p>

          <!--<div class="field">-->
          <!--<div class="control">-->
          <!--<input class="input is-medium" type="email" placeholder="Your Email" autofocus="">-->
          <!--</div>-->
          <!--</div>-->
          <div
            v-if="error"
            class="message is-danger">
            <div class="message-body">
              {{ error }}
            </div>
          </div>

          <b-field>
            <b-input
              id="first_name"
              v-model="form.first_name"
              ttpe="text"
              required
              autofocus
              size="is-medium"
              placeholder="First name"
            />
          </b-field>

          <b-field>
            <b-input
              id="email"
              v-model="form.email"
              ttpe="text"
              required
              autofocus
              size="is-medium"
              placeholder="Email"
            />
          </b-field>

          <b-field>
            <b-input
              id="password"
              v-model="form.password"
              type="password"
              required
              password-reveal
              size="is-medium"
              placeholder="Password"
            />
          </b-field>

          <b-field>
            <b-input
              id="confirm_password"
              v-model="form.confirm_password"
              type="password"
              password-reveal
              required
              size="is-medium"
              placeholder="Confirm password"
            />
          </b-field>

          <b-field class="has-text-left">
            <b-checkbox v-model="form.checked">
              Remember me
            </b-checkbox>
            <!--<a style="float: right" href="/">login?</a>-->
          </b-field>


          <button
            id="btn_sign_up"
            :class="{'is-loading': isLoading, 'is-disabled': isLoading}"
            class="button is-block is-primary is-outlined is-medium is-fullwidth"
            @click="onSubmit($event)">
            Sign up
          </button>

        </form>
      </div>
    </div>
  </div>
</template>


<script>
  import gdprConsent from "@/components/GdprConsent";
  import GoogleSignInButton from 'vue-google-signin-button-directive'

  export default {
    name: 'SignUp',
    components: {
      gdprConsent
    },
    directives: {
      GoogleSignInButton
    },
    data() {
      return {
        gdprConsented: Vue.cookie.get('gdprConsented'),
        error: null,
        form: {
          first_name: '',
          email: '',
          username: '',
          password: '',
          confirm_password: ''
        },
        redirect: null,
        isLoading: false,
        googleIsLoading: false,
        googleSignInParams: {
          client_id: '476667578043-0tm7eoufgmi6m961qb2m77hhelj5ksl5.apps.googleusercontent.com',
          response_type: 'permission'
        }
      }
    },
    created() {
      // this.$store.dispatch('setLayout', 'front-layout')
    },

    methods: {
      onSubmit(evt) {
        this.isLoading = true
        evt.preventDefault()
        this.$auth.register({
          body: this.form,
          autoLogin: true,
          redirect: this.redirect || {name: 'FrontPage'},
          rememberMe: this.form.checked
        }).then((res) => {
          this.$mixpanel.alias(res.body.data.id);
          document.querySelector('button.modal-close').click();
          this.$store.dispatch("setUser", res.body.data);
        }, (res) => {
          console.log('error ' + this.context);
          this.error = res.data.message;
          this.isLoading = false
        });
      },
      onClick(e) {
        e.preventDefault();
        this.$router.push('/')
      },
      OnGoogleAuthSuccess(access_token) {
        this.$store.dispatch("setUserFromGoogle", {
          access_token,
          pid: this.form.pid,
        }).then((res) => {
          if (!res.userExisted) {
            this.$mixpanel.alias(res.user.id+'');
          }
          this.$store.dispatch("setUser", res.user);
          this.$auth.user(res.user)
          this.$auth.token(null, res.token)
          this.$auth.watch.authenticated = true
          this.$auth.watch.loaded = true
          this.googleIsLoading = false;
          // this.$store.dispatch("setLayout", "logged-in-layout");
          this.$cookie.set('rememberMe', true, 365)
          // this.$router.push({name: 'Home'})
          this.$router.push({name: 'FrontPage'});
          document.querySelector('button.modal-close').click()
        });
      },
      OnGoogleAuthFail (error) {
        console.log(error)
        this.googleIsLoading = false;
      }
    }
    /*created(evt) {
    console.log(evt)
      evt.preventDefault()
      axios.post(`http://localhost:3003/api/register`, this.form)
      .then(response => {
        // JSON responses are automatically parsed.
        this.form = response.data
      })
      .catch(e => {
        console.log(e)
      })


    },*/
  }
</script>


