<template lang="pug">
  div
    .tag-filters.has-tag-uppercase.has-flex-center-items.mb-3.has-flex-wrap
      b-tag.ml-2.smooth-bg-fade.mb-1(
        v-for="(tag, tagId) in tags"
        :key="tagId"
        :type="tags[tagId].color ? ((!currentTagId || tagId == currentTagId) ? `is-${tags[tagId].color}` : '') : ''"
        v-if="tagId && tags[tagId] && tags[tagId].icon && usedTagIds.indexOf(tagId-0) !== -1"
        )
        span.is-clickable.has-flex-center-items(
          @click="filterOn(tagId, tags[tagId].name)"
          )
          b-icon.mr-1(
            :icon="tags[tagId].icon"
            v-if="tags[tagId].icon"
            custom-size="is-size-6"
            )
          | {{tags[tagId].name}}
    transition(name="list" mode="out-in")
      .box.links-list(:key="current")
        link-item(
          app-loaded
          v-for="(link, idx) in filteredLinks"
          :link="link"
          :key="link.id"
          :index="idx"
          v-if="idx < perPage*current && idx >= (perPage*current - perPage)"
          @edit="openEditLinkModal(link)"
          )
    b-pagination(
    v-if="filteredLinks.length > perPage"
    :total="filteredLinks.length"
    :current.sync="current"
    size="is-small"
    :per-page="perPage")

</template>

<script>
import _uniq from "lodash/uniq";
import _flatten from "lodash/flatten";
import {Link} from "@/store/models/resources";
import linkItem from "@/components/Link";
import linkModal from '@/components/LinkModal';
import { mapGetters } from "vuex";

export default {
  name: "LinksList",
  components: {
    linkModal,
    linkItem
  },
  data() {
    return {
      current: 1,
      perPage: 10,
      editLinkModal: false,
      link: null
    };
  },
  computed: {
    ...mapGetters(["links", "tags"]),
    usedTagIds() {
      return _uniq(_flatten(this.links.map((link) => link.tags)));
    },
    currentTagId() {
      return this.$route.params.tagId;
    },
    filteredLinks() {
      const { tagId } = this.$route.params;
      if (tagId) {
        const id = parseInt(tagId);
        return this.links.filter((link) => link.tags.indexOf(id) !== -1)
      } else {
        return this.links
      }
    }
  },
  created() {
    this.$store.dispatch('getVotes');
    this.$store.dispatch('getLinks').then(() => {
      setTimeout(function() {
        document.dispatchEvent(new Event('x-app-rendered'));
      }, 500);
    });
  },
  methods: {
    filterOn(tagId, tagName) {
      if (this.$route.params.tagId && this.$route.params.tagId == tagId) {
        this.$router.push({ name: 'FrontPage' });
      } else {
        this.$router.push({ name: 'SortByTagPage', params: { tagId, tagName }})
      }
    },
    openEditLinkModal(link) {
      this.$modal.open({
        parent: this,
        props: {
          mode: 'edit',
          editLink: link,
        },
        component: linkModal,
        hasModalCard: false
      })
    },
  }
};
</script>

<style lang="scss">
@import "colors";

.smooth-bg-fade {
  transition: background-color .2s ease, color .2s ease;;
}

.links-list>.columns {
  transition: opacity .2s ease;
}
.links-list:hover>.columns {
  opacity: 0.2;
}
.links-list:hover>.columns:hover {
  opacity: 1;
}

.link-item:last-child .link--description {
  bottom: 100% !important;
  top: inherit !important;
}

.list-enter-active, .list-leave-active {
  transition: all .2s;
}
.list-enter {
  opacity: 0;
  transform: translateX(50px);
}
.list-leave-to {
  opacity: 0;
  transform: translateX(-50px);
}


</style>
