;AAEA;EACA,gBAAA;AACA;AAEA;EACA,iBAAA;AACA;AAEA;EACA,qBAAA;AACA;AACA;EACA,eAAA;AACA;AAEA;;EAEA,gBAAA;AACA;AAEA;EACA,YAAA;EACA,iBAAA;AACA;AAEA;EACA,0BAAA;EACA,2CAAA;AACA;AAEA;;EAEA,SAAA;AACA;AAEA;EACA,kBAAA;EACA,WAAA;EACA,mBAAA;EACA,aAAA;EACA,YAAA;EACA,uBAAA;AACA;AAEA;EACA,qBAAA;EACA,kBAAA;EAEA,kDAAA;EACA,mCAAA;EACA,SAAA;EACA,YAAA;EACA,YAAA;EACA,qBAAA;EACA,kBAAA;EAEA,kCAAA;EACA,eAAA;EACA,sBAAA;EACA,yBAAA;EACA,qBAAA;EACA,iBAAA;EACA,+BAAA;EACA,YAAA;EACA,YAAA;AACA;IACA,WAAA;IACA,YAAA;AACA;AACA;AACA;;EAEA,WAAA;AACA;AAEA;;EAEA,eAAA;EACA,iBAAA;EACA,4BAAA;EACA,2BAAA;AACA","file":"MyAccount.vue?vue&type=style&index=0&id=eb6a0344&scoped=true&lang=css&","sourcesContent":["<template lang=\"pug\">\n  .box\n    .message.is-danger(v-if=\"error\")\n      .message-body\n        | {{ error }}\n    .columns\n      .column\n        b-field.has-text-left(label=\"First name\")\n          b-input#first_name(v-model=\"user.first_name\" @keyup.native=\"delayedSave\")\n      .column\n        b-field.has-text-left(label=\"Last name\")\n          b-input#last_name(v-model=\"user.last_name\" @keyup.native=\"delayedSave\")\n    b-field.has-text-left(label=\"Email\")\n      b-input#email(v-model=\"user.email\" type=\"text\" required=\"\" size=\"is-medium\" placeholder=\"Email\")\n    b-field.has-text-left(label=\"New password\")\n      b-input#password(v-model=\"user.newPassword\" type=\"password\" size=\"is-medium\")\n    button#btn_save_profile.button.is-block.is-primary.is-medium.is-fullwidth(:class=\"{'is-loading': isLoading, 'is-disabled': isLoading, 'is-success': isSuccess}\" @click.stop.prevent=\"save\")\n      b-icon(v-if=\"isSuccess\" icon=\"check\")\n      span(v-else=\"\")\n        | Save\n\n</template>\n\n\n<script>\nimport { mapGetters } from \"vuex\";\nimport { User } from \"@/store/models/resources\";\nimport map from \"lodash/map\";\nimport difference from \"lodash/difference\";\nimport sortBy from \"lodash/sortBy\";\nimport findIndex from \"lodash/findIndex\";\n\nexport default {\n  name: \"MyAccount\",\n  components: {\n  },\n  data() {\n    return {\n      isConnecting: false,\n      currencies: ['EUR', 'USD', 'GBP'],\n      error: null,\n      isLoading: false,\n      isSuccess: false,\n      showTooltip: false,\n      user: {}\n    };\n  },\n  computed: {\n    ...mapGetters([\"apiUrl\"]),\n    name() {\n      return (\n        this.user.name ||\n        `${this.user.first_name || \"\"} ${this.user.last_name || \"\"}`\n      );\n    },\n    location() {\n      if (\n        this.user.city &&\n        this.user.city.length &&\n        this.user.country &&\n        this.user.country.length\n      ) {\n        return `${this.user.city}, ${this.user.country}`;\n      }\n    },\n    picture() {\n      if (\n        this.user.picture &&\n        (this.user.picture.substring(0, 4) === \"data\" ||\n          this.user.picture.substring(0, 4) === \"http\")\n      )\n        return this.user.picture;\n      else if (this.user.picture) return `${this.apiUrl}/${this.user.picture}`;\n      else return \"/static/logo-teacher-tool.png\";\n    },\n    remainingQuantities() {\n      const available = [3,4,5,8,10]\n      const used = map(this.user.discounts, 'quantity')\n      return difference(available, used);\n    },\n    sortedDiscounts() {\n      if (!this.user.discounts || !this.user.discounts.length) {\n        return []\n      }\n      return sortBy(this.user.discounts, ['quantity'])\n    }\n  },\n  created() {\n    // console.log('user: ', this.$auth.check())\n    // console.log('user: ', this.$auth.user())\n    // this.user = Object.assign({}, this.$auth.user())\n    this.user = this.$auth.user();\n    if (!this.user.picture) {\n      var cpnt = this;\n      setTimeout(() => {\n        cpnt.showTooltip = true;\n      }, 400);\n      setTimeout(() => {\n        cpnt.showTooltip = false;\n      }, 4000);\n    }\n\n  },\n  mounted() {\n    console.log(this.$route)\n    if (this.$route.query && this.$route.query.code) {\n      this.isConnecting = true\n      User.stripeConnect(this.$route.query).then((data) => {\n        console.log(data)\n        if (data.body.user) {\n          this.$store.dispatch(\"setUser\", data.body.user);\n          this.$auth.user(data.body.user);\n          this.user = data.body.user\n        }\n        this.isConnecting = false\n      })\n    }\n  },\n  methods: {\n    delayedSave: _.debounce(function() {\n      console.log(\"delaying\");\n      this.save();\n    }, 700),\n    save() {\n      this.isLoading = true;\n      this.$store.dispatch(\"saveUser\", this.user).then(data => {\n        this.$store.dispatch(\"setUser\", data.user);\n        this.$auth.user(data.user);\n        this.isLoading = false;\n        this.isSuccess = true;\n        setTimeout(() => {\n          this.isSuccess = false;\n        }, 2500);\n      });\n    },\n    pictureUploaded(file) {\n      console.log(file);\n      this.user.picture = file.dataURL;\n    },\n    pictureUploadError(file, message, xhr) {\n      console.log(file, message, xhr);\n      this.$toast.open({\n        duration: 5000,\n        message: message,\n        type: \"is-danger\"\n      });\n    },\n    noop() {},\n    cancelSubscription() {\n      this.$store.dispatch(\"unsubscribe\").then(data => {\n        this.isLoading = false;\n        this.$store.dispatch(\"setUser\", data.user);\n        this.$auth.user(data.user);\n        this.user = data.user;\n\n        this.$toast.open({\n          duration: 3000,\n          message: `Subscription successfully canceled`,\n          type: \"is-success\"\n        });\n      });\n    },\n    addDiscount() {\n      this.user.discounts = this.user.discounts || []\n      this.user.discounts.push({\n        quantity: null,\n        percent: 20\n      })\n    },\n    removeDiscount(quantity) {\n      const idx = findIndex(this.user.discounts, {quantity: quantity});\n      this.user.discounts.splice(idx, 1);\n      this.delayedSave();\n    }\n  }\n};\n</script>\n\n\n<style scoped>\n.avatar {\n  margin-top: -70px;\n  padding-bottom: 20px;\n}\n\ninput {\n  font-weight: 300;\n}\n\np {\n  font-weight: 700;\n}\n\np.subtitle {\n  padding-top: 1rem;\n}\n\na {\n  text-decoration: none;\n}\n.equal-height {\n  padding: 15px 0;\n}\n\n#btn_canceled_subscription,\n#btn_cancel_subscription {\n  margin-top: 30px;\n}\n\n.container {\n  width: 400px;\n  margin-top: 100px;\n}\n\n.dz-drag-hover {\n  border: 2px dashed #dedede;\n  background-color: rgba(240, 248, 255, 0.34);\n}\n\n.tooltip.is-right:before,\n.tooltip.is-right:after {\n  top: -30%;\n}\n\n#picture_dropzone {\n  position: relative;\n  z-index: 10;\n  border-radius: 100%;\n  height: 100px;\n  width: 100px;\n  margin: -126px auto 0px;\n}\n\n.connect-button {\n  display: inline-block;\n  margin-bottom: 1px;\n  background-image: -webkit-gradient(linear,left top,left bottom,from(#28a0e5),to(#015e94));\n  background-image: linear-gradient(#28a0e5,#015e94);\n  -webkit-font-smoothing: antialiased;\n  border: 0;\n  padding: 1px;\n  height: 32px;\n  text-decoration: none;\n  border-radius: 4px;\n  -webkit-box-shadow: 0 1px 0 rgba(0,0,0,.2);\n  box-shadow: 0 1px 0 rgba(0,0,0,.2);\n  cursor: pointer;\n  -moz-user-select: none;\n  -webkit-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n  text-decoration: none!important;\n  width: 200px;\n  height: 32px;\n  img {\n    width: 100%;\n    height: 32px;\n  }\n}\n.discount_quantity,\n.discount_percent {\n  width: 43px;\n}\n\n.discounts input,\n.discounts .control {\n  font-size: 12px;\n  line-height: 26px;\n  margin-right: 3px !important;\n  margin-left: 2px !important;\n}\n\n</style>\n"]}]);

