<template lang="pug">
  .columns.has-text-left.has-flex-start-items.link-item.has-position-relative.is-multiline
    .column.column-index
      | {{ '#'+(index + 1)}}
      p.link--upvote.mt-1
        button.button.is-white(@click="votes[link.id] ? cancelVote() : upvote()")
          b-icon(
            icon="arrow-drop-up"
            :type="votes[link.id] ? 'is-success' : 'is-white'"
            )
    .column
      .w-100
        a.link--title(
          :href="link.url"
          rel="nofollow"
          target="_blank"
        )
          h2
            | {{link.title}}
            small.ml-2 ({{link.url.match(/((\w\.)?\w+\.[^/]+)/)[0]}})
      .has-flex-center-items.mt-2
        p.link--user
          user-picture.mr-2(:user="link.user")
          span.has-text-grey-light.mr-2 {{ link.user.first_name }}
        span.has-text-grey-light.is-size-7.mr-2 {{ link.created_at }} ago
        span.has-text-grey-light.mr-2.is-size-7.has-flex-center-items.is-inline-flex
          i.mdi.mdi-arrow-drop-up.is-size-6.link--votes-nb
          | {{ link.votes_nb }}

        p.anim-slide-hover.link--actions.ml-3.is-inline-block
          button.button.is-small(
          v-if="user.id && user.id != link.user.id"
          @click="votes[link.id] ? cancelVote() : upvote()"
          :class="{'has-text-success': votes[link.id]}"
          ) +1

          button.button.is-small.ml-2(
            v-if="user.id && user.id == link.user.id"
            @click="$emit('edit', link)"
          ) Edit

          button.button.is-small.ml-2(
            v-if="user.id && user.id == link.user.id"
            aria-label="Delete the resource?"
            v-confirm="{ok: dialog => remove(link), message: 'Are you sure you want to <b>delete the resource</b>?'}")
            | Delete

      p.link--description.anim-slide-hover(v-if="link.description !== 'null'")
        | {{ link.description }}
    .column.is-narrow.has-text-right.has-flex-center-items.has-tag-uppercase
      b-tag.ml-2(
        v-for="tagId in link.tags"
        :key="tagId"
        :type="tags[tagId].color ? `is-${tags[tagId].color}` : ''"
        v-if="tagId && tags[tagId]"
        )
        span.is-clickable.has-flex-center-items(
          @click="filterOn(tagId, tags[tagId].name)"
        )
          b-icon.mr-1(
            :icon="tags[tagId].icon"
            v-if="tags[tagId].icon"
            custom-size="is-size-6"
            )
          | {{tags[tagId].name}}



</template>

<script>
import { mapGetters } from "vuex";
import userPicture from "@/components/UserPictureUI";

/*
*   b-tooltip.w-100(
        :label="''"
        type='is-light'
        multilined
        size="is-large"
        :animated='true'
        position='is-left')
* */

export default {
  name: "Link",
  props: [
    'index',
    'link'
  ],
  components: {
    userPicture
  },
  computed: mapGetters(["tags", "user", "votes"]),
  data() {
    return {
    };
  },
  methods: {
    filterOn(tagId, tagName) {
      if (this.$route.params.tagId && this.$route.params.tagId == tagId) {
        this.$router.push({ name: 'FrontPage' });
      } else {
        this.$router.push({ name: 'SortByTagPage', params: { tagId, tagName }})
      }
    },
    remove(link) {
      this.$store.dispatch('deleteLink', link)
    },
    upvote() {
      this.$store.dispatch('saveVote', this.link)
    },
    cancelVote(id) {
      this.$store.dispatch('deleteVote', this.votes[this.link.id])
    },
  }
};
</script>

