// Vue.http.headers.common['Access-Control-Allow-Headers'] = 'Origin, Accept, Content-Type, Authorization, Access-Control-Allow-Origin'


import { crystalApi, railsApi, nodeApi } from "../../config";

if (process.env.NODE_ENV !== "production") {
  Vue.http.options.root = crystalApi;
} else {
  Vue.http.options.root = crystalApi;
}
// Vue.http.options.xhr = {withCredentials: true};

export const Vote = Vue.resource(`${crystalApi}/votes{/id}`)
export const Link = Vue.resource(`${crystalApi}/links{/id}`, {}, {
  analyse: { method: "GET", url: `${crystalApi}/analyse_link` },
});
export const Tag = Vue.resource(`${crystalApi}/tags{/id}.json`);
export const User = Vue.resource(
  `${nodeApi}/users{/id}`,
  {},
  {
    resetPasswordEmail: { method: "POST", url: `${nodeApi}/auth/reset_password_email` },
    resetPassword: { method: "POST", url: `${nodeApi}/auth/reset_password` },
    authGoogle: { method: "POST", url: `${nodeApi}/auth/google` },
  }
);

