<template lang="pug">
  div
    .card.share-resource-modal(
      :class="{'needs-auth': !user.id}"
      v-if="!isSignUp"
      )
      header.card-header
        p.card-header-title.is-size-4
          | {{ mode == 'create' ? 'Share a resource' : 'Edit a resource' }}
      .card-content.has-text-left
        .step1
          b-field(:label="isAnalysed ?  'URL' : 'Paste URL here:'")
            b-input#url_input(
            v-model="link.url"
            :disabled="isAnalysed"
            icon="link"
            type="url"
            @keyup.native.enter="analyse"
            placeholder="Ex: https://materialmaker.io"
            )
          div.has-text-right(v-show="!isAnalysed")
            button.button.is-primary(
            :class="{'is-loading': isAnalysing}"
            :disabled="isAnalysing"
            @click="analyse") Continue

        .step2(v-show="isAnalysed")
          b-field(label="Title")
            b-input#title_input(
            v-model="link.title"
            type="text"
            icon="title"
            )
          b-field(label="Description")
            b-input(
            v-model="link.description"
            icon="short-text"
            type="textarea"
            )

          b-field(label="Tags")
            b-taginput(
            v-model="link.tags"
            :data="filteredTags"
            field="name"
            :autocomplete="true"
            :allow-new="true"
            ellipsis
            icon="label"
            placeholder="Tag"
            @typing="getFilteredTags"
            )

          div.has-text-right
            button.button.is-primary(
            :class="{'is-loading': isLoading, 'is-success': isSuccess}"
            :disabled="isLoading"
            @click="share")
              b-icon(v-if="isSuccess" icon="check")
              span(v-else="")
                | {{ mode == 'create' ? 'Share' : 'Edit' }}
    .needs-auth-button(v-if="!user.id && !isSignUp")
      p.mb-2.w-100.has-text-info
        | You need to
        a.has-text-info.mx-1.has-text-underline.is-underlined(@click.prevent="login" href="#")
          | login
        | or sign up to share a resource
      button.button.is-primary(@click="isSignUp = true")
        | Sign up now
    div(v-if="isSignUp")
      sign-up


</template>

<script>
import _compact from "lodash/compact";
import { mapGetters } from "vuex";
import {Link} from '@/store/models/resources';
import signUp from '@/components/SignUp';

export default {
  name: "Link",
  props: [
    'mode',
    'editLink'
  ],
  components: {
    signUp
  },
  computed: {
    ...mapGetters(["tags", "user"]),
    tagsAsArray: function () {
      const tags = []
      for (let key in this.tags) {
        tags.push(this.tags[key]);
      }
      return tags
    }
  },
  data() {
    return {
      isSignUp: false,
      isSuccess: false,
      isLoading: false,
      isAnalysing: false,
      isAnalysed: false,
      filteredTags: [],
      link: {
        url: null,
        title: null,
        description: null,
        tags: [],
      }
    };
  },
  mounted() {
    if (this.mode !== 'create') {
      this.isAnalysed = true;
      Object.assign(this.link, this.editLink);
      this.link.tags = _compact(this.editLink.tags).map((ta) => (this.tags[ta]) ? this.tags[ta].name : ta)
    } else {
      if (this.user.id) {
        this.focusOnInput()
      }
    }
  },
  methods: {
    login() {
      this.$router.push({ name: 'SignIn' });
      this.$emit('close');
    },
    resetModal() {
      this.isSuccess = false;
      this.isLoading = false;
      this.isAnalysing = false;
      this.isAnalysed = false;
      this.link = {
        url: null,
        title: null,
        description: null,
        tags: [],
      }
    },
    getFilteredTags(text) {
      this.filteredTags = this.tagsAsArray.filter((option) => {
        return option
          .name
          .toLowerCase()
          .indexOf(text.toLowerCase()) !== -1
      })
    },
    analyse() {
      this.isAnalysing = true;
      if (this.link.url.match(/(.jpe?g|.png|.bmp|.mp4|.mov|.avi)$/)) {
        this.isAnalysing = false;
        this.isAnalysed  = true;
      } else {
        Link.analyse({url: this.link.url}).then((res) => {
          Object.assign(this.link, res.data);
          // }).catch((err) => {
          //   console.log(err)
          // }).finally(() => {
          this.isAnalysing = false;
          this.isAnalysed = true;
        }, (err) => {
          this.isAnalysing = false;
          this.isAnalysed = true;
        })
      }
    },

    share() {
      this.isLoading = true;
      this.$store.dispatch('saveLink', this.link).then((res) => {
        this.isLoading = false;
        this.isSuccess = true;
        setTimeout(() => {
          this.$emit('close');
          this.isSuccess = false;
          setTimeout(() => {
            this.resetModal();
          })
        }, 1000)
      });
    },
    focusOnInput() {
      setTimeout(() => {
        document.getElementById('url_input').focus();
      }, 300)
    }

  }
};
</script>

<style lang="scss" scoped>
  @import "colors";
  h2 {
    font-weight: 700;
  }
  small {
    font-weight: 300;
  }

  .needs-auth-button {
    text-align: center;
    top: 70px;
    display: flex;
    position: absolute;
    width: 100%;
    justify-content: center;
    flex-wrap: wrap;
  }


  .needs-auth {
    position: relative;
    &:after {
      content: ' ';
      background-color: rgba(255, 255, 255, 0.55);
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: block;
      position: absolute;
    }
  }

</style>
