import Vue from "vue";
import Router from "vue-router";
import FrontPage from "@/components/FrontPage";
const SignUp = () => import("@/components/SignUp");
const SignIn = () => import( "@/components/SignIn");
const MyAccount = () => import( "@/components/MyAccount");
const PrivacyPage = () => import("@/components/PrivacyPage");

Vue.use(Router);

export default new Router({
  mode: "history",
  routes: [
    {
      path: "/",
      name: "FrontPage",
      component: FrontPage
    },
    {
      path: "/tag/:tagId/:tagName",
      name: "SortByTagPage",
      component: FrontPage
    },
    {
      path: "/profile",
      name: "MyAccount",
      component: MyAccount
    },
    {
      path: "/sign_up",
      name: "SignUp",
      component: SignUp
    },
    {
      path: "/sign_in",
      name: "SignIn",
      component: SignIn
    },
    {
      path: "/privacy",
      name: "PrivacyPage",
      component: PrivacyPage
    },
  ]
});
