<template lang="pug">
  div#search_everywhere(
  v-shortkey="['shift','/']"
  @shortkey="search()"
  )
    i.fa.fa-spinner.fa-spin(v-if="loading")
    template(v-else="")
      i.fa.fa-search(v-show="isEmpty")
      i.fa.fa-times(v-show="isDirty" @click="reset")
    // the input field
    b-field
      b-input#search_everywhere_btn(
      icon="search"
      type="search"
      placeholder="Search"
      autocomplete="off"
      v-model="query"
      v-shortkey="['s']"
      @shortkey.native="search()"
      @keydown.native.prevent.down="down"
      @keydown.native.prevent.up="up"
      @keydown.native.enter="hit"
      @keydown.native.esc="reset"
      @input="update")
    <!--@blur="reset"-->
    // the list
    ul.box.has-text-left(v-show="hasItems")
      // for vue@1.0 use: ($item, item)
      li(v-for="(option, $item) in items" :class="activeClass($item)" @mousedown="hit" @mousemove="setActive($item)")
        span(v-if="option.type === 'l'")
          | {{ option.title }}

    ul.box(v-show="query && query.length && !hasItems")
      li(@mousedown="reset") No results


</template>

<script>
import VueTypeahead from "vue-typeahead";
import _toLower from "lodash/toLower";
import _filter from "lodash/filter";
import _includes from "lodash/includes";
import { mapGetters } from "vuex";

export default {
  name: "SearchEverywhere",
  components: {
  },
  extends: VueTypeahead,
  data() {
    return {
      query: null,
      src: "...",
      limit: 10,
      minChars: 2,
      selectFirst: true
    };
  },
  computed: {
    ...mapGetters(["links"]),
    options() {
      var opts = [];
      _.each(this.links, function(link) {
        opts.push({
          type: "l",
          title: link.title,
          desc: link.description,
          link: `${link.url}`
        });
      });

      return opts;
    }
  },
  methods: {
    redirectWhenSelect(selection) {
      if (selection) {
        this.$router.push(selection.link);
      }
    },
    onHit(selection) {
      console.log("hitting ", selection);
      if (selection && selection.link) {
        // this.$router.push(selection.link);
        window.open(selection.link, "_blank");
        this.reset();
      }
    },
    fetch() {
      return new Promise((resolve, reject) => {
        const query = _toLower(this.query);
        console.log(this.options);
        resolve({
          data: _filter(this.options, opt =>
            _includes(_toLower(opt.title || opt.desc || ""), query)
          )
        });
        reject();
      });
    },
    search() {
      console.log("triggering");
      document.getElementById("search_everywhere_btn").focus();
    }
  }
};
</script>

