const settings = {}

if (process.env.NODE_ENV !== 'production' && process.env.NODE_ENV !== 'staging') {
  settings.crystalApi = 'http://127.0.0.1:3000/v3'
  settings.nodeApi = 'http://127.0.0.1:8081/api/v2'
} else if (process.env.NODE_ENV === 'staging') {
  settings.crystalApi = 'https://news-staging.teachee.io/v3';
  settings.nodeApi = 'https://news.teachee.io/v2';
} else {
  settings.crystalApi = 'https://news.teachee.io/v3';
  settings.nodeApi = 'https://news.teachee.io/v2';
}

export const crystalApi = settings.crystalApi;
export const nodeApi = settings.nodeApi;
